// ** ACÁ EN REDUCER CREO MI ESTADO INICIAL - 
// ** Y HAGO LA LOGICA DE MIS FILTRADOS

const initialState = {
    recipes : []
}

function rootReducer(state =  initialState, action){
    switch(action.type){
        case 'GET_GAMES':
            return{
                ...state,
                games: action.payload,
                allGames: action.payload
            }        
        case 'GET_NAME_GAMES':
            return{
                ...state,
            }  
        case 'GET_GENRES':
            return{
                ...state,
            }  
        case 'FILTER_BY_STATUS':
            return{
                ...state,
            }     
        case 'POST_GAMES':
            return{
                ...state,
            }  
        case 'FILTER_CREATED':
            return{
                ...state,
            }
        case 'ORDER_BY_NAME':
            return{
                ...state,
            }
        default:
                return state;
        }
    }

export default rootReducer;